<style>
/* @import "https://cdn.syncfusion.com/18.1.0.42/js/web/flat-saffron/ej.web.all.min.css"; */
  @import "https://cdn.boldreports.com/2.2.32/content/material/bold.reports.all.min.css";
</style>

<template>
  <div>
    <vx-card ref="MainCard">
      <div>
        <vs-table
          :sst="true"
          ref="gridTable"
          v-model="selected"
          pagination
          :max-items="itemsPerPage"
          search
          :data="reportData"
          class="vs-con-loading__container"
        >
          <div slot="header" class="flex flex-wrap-reverse items-center flex-grow">
            <!-- ADD NEW -->
            <vs-button color="warning" type="gradient" @click="GetGrid()"        icon-pack="feather" icon="icon-refresh-ccw">Reload</vs-button>
            <!-- ITEMS PER PAGE -->
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-4">
              <div
                class="p-4 border border-solid border-grey-light rounded-full d-theme-dark-bg cursor-pointer flex items-center justify-between font-medium"
              >
                <span
                  class="mr-2"
                >{{ currentPage * itemsPerPage - (itemsPerPage - 1) }} - {{ reportData.length - currentPage * itemsPerPage > 0 ? currentPage * itemsPerPage : reportData.length }} of {{ reportData.length }}</span>
                <feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
              </div>
              <!-- <vs-button class="btn-drop" type="line" color="primary" icon-pack="feather" icon="icon-chevron-down"></vs-button> -->
              <vs-dropdown-menu>
                <vs-dropdown-item @click="itemsPerPage=10">
                  <span>10</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=20">
                  <span>20</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=50">
                  <span>50</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="itemsPerPage=100">
                  <span>100</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
          <template slot="thead">
            <vs-th sort-key="Id">Tag</vs-th>
            <vs-th sort-key="Name">Operator</vs-th>
            <vs-th>Action</vs-th>
          </template>
          <!-- visible: false -->
          <template slot-scope="{data}">
            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
              <vs-td :data="tr.tag">{{tr.id}}</vs-td>
              <vs-td :data="tr.operator">{{tr.name}}</vs-td>
              <vs-td class="whitespace-no-wrap">
                <feather-icon
                  icon="EyeIcon"
                  svgClasses="w-5 h-5 hover:text-primary stroke-current"
                  @click.stop="editDataSidebar(tr)"
                />
               
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </div>
    </vx-card>
     <vs-spacer></vs-spacer>
      <div>
      <vx-card id="reportViewerCard" class="vs-con-loading__container" ref="reportInfo" v-show="!isInfoHidden">
        <!-- Creating a div tag which will act as a container for boldReportViewer widget.-->
        <div style="height: 100%; width: 99.85%; min-height: 750px" id="viewer" ref="viewer"></div>

        <vs-divider class="mb-6 pt-0"></vs-divider>
        <div class="vx-row">
          <vs-spacer></vs-spacer>
          <div class="vx-col">
             <vs-button color="warning" type="border" class="mb-2"  @click="cancelReportViewer">Close</vs-button>
          </div>
        </div>
      </vx-card>

    </div>
  </div>
</template>

<script>
 
 //Bold Components
//  import jQuery from './bold_scripts/jquery.js';
//  import boldReportsCommon from './bold_scripts/common/bold.reports.common.min.js';
//  import boldReportsWidgets from './bold_scripts/common/bold.reports.widgets.min.js';
//  import ejBase from './bold_scripts/common/ej2-base.min.js';
//  import ejData from './bold_scripts/common/ej2-data.min.js';
//  import ejPdfExport from './bold_scripts/common/ej2-pdf-export.min.js';
//  import sjSvgBase from './bold_scripts/common/ej2-svg-base.min.js';

 //EJ 1
// import jQuery from './java_scripts/jquery-1.10.2.min.js';
// import ejcore from './java_scripts/common/ej.core.js';
// import ejdata from './java_scripts/common/ej.data.js';
// import ejwebform from './java_scripts/common/ej.webform.js';
// import ejglobalize from './java_scripts/common/ej.globalize.js';
// import ejchart from './java_scripts/datavisualization/ej.chart.js';
// import ejDropDownList from './java_scripts/web/ej.dropdownlist.js';
// import ejScroller from './java_scripts/common/ej.scroller.js';
// import ejToolbar from './java_scripts/web/ej.toolbar.js';
// import ejTooltip from './java_scripts/web/ej.tooltip.js';
// import ejButton from './java_scripts/web/ej.button.js';
// import ejWaitingpopup from './java_scripts/web/ej.waitingpopup.js';
// import ejDatepicker from './java_scripts/web/ej.datepicker.js';
// import canvasAreaDraw from './java_scripts/jquery.canvasAreaDraw.js';


import axios from "axios";
import flatPickr from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import vSelect from "vue-select";

export default {
  data() {
    return {

      operatorTypeSelect:0,
      operatorTypeData:[],

      isListHidden: false,   
      isInfoHidden: true,

      itemsPerPage: 10,
      selected: [],
      reportData: [],
    
      isMounted: false,
      formData: {
         id:0,
         accountId:0,
         operatorTypeId:0,
         operatorType: "None",
         name:"None",
         enable:true,
         description:"None",
         account:"",       
         tag  :"",       
         perOperatorDataStore:
          [{
            id:0,
            storeId:0,
            operatorId:0,
            download:0,
          }]
        }   
    };
  },
  components: {
    flatPickr,
    "v-select": vSelect,

  },
  computed: {
    currentPage() {
      if (this.isMounted) {
        return this.$refs.gridTable.currentx;
      }
      return 0;
    }
  },
  mounted() {
    this.GetGrid();
    this.isMounted = true;


    let jQuery = document.createElement('script')
    jQuery.setAttribute('src','https://cdn.syncfusion.com/js/assets/external/jquery-1.10.2.min.js')
    document.head.appendChild(jQuery)

    let jseasing = document.createElement('script')
    jseasing.setAttribute('src','https://cdn.syncfusion.com/js/assets/external/jquery.easing.1.3.min.js')
    document.head.appendChild(jseasing)

    let jsrender = document.createElement('script')
    jsrender.setAttribute('src','https://cdn.syncfusion.com/js/assets/external/jsrender.min.js')
    document.head.appendChild(jsrender)

    // let ejcore = document.createElement('script')
    // ejcore.setAttribute('src','https://cdn.syncfusion.com/18.1.0.42/js/common/ej.core.js')
    // document.head.appendChild(ejcore)

    let ejAll = document.createElement('script')
    ejAll.setAttribute('src','https://cdn.syncfusion.com/16.4.0.54/js/web/ej.web.all.min.js')
    document.head.appendChild(ejAll)

    let ej2base = document.createElement('script')
    ej2base.setAttribute('src','https://cdn.boldreports.com/2.2.32/scripts/common/ej2-base.min.js')
    document.head.appendChild(ej2base)

    let ej2data = document.createElement('script')
    ej2data.setAttribute('src','https://cdn.boldreports.com/2.2.32/scripts/common/ej2-data.min.js')
    document.head.appendChild(ej2data)

    let ejExport = document.createElement('script')
    ejExport.setAttribute('src','https://cdn.boldreports.com/2.2.32/scripts/common/ej2-pdf-export.min.js')
    document.head.appendChild(ejExport)

    // let ejtoolbar = document.createElement('script')
    // ejtoolbar.setAttribute('src','https://cdn.syncfusion.com/18.1.0.42/js/web/ej.toolbar.js')
    // document.head.appendChild(ejtoolbar)

    // let ejtooltip = document.createElement('script')
    // ejtooltip.setAttribute('src','https://cdn.syncfusion.com/18.1.0.42/js/web/ej.tooltip.js')
    // document.head.appendChild(ejtooltip)


  },
  methods: {
     cancelReportViewer() {
        var self = this;

        self.isInfoHidden = true;
        //self.GetGrid();
      },

  //********************* System related*/
   successNotify() {
      this.$vs.notify({
        title: 'Successfully',
        text: 'Item successfully added | updated | removed',
        color: 'success'
      })
    },
  FailedNotify() {
      this.$vs.notify({
        title: 'Not Successfull',
        text: 'Item not added',
        color: 'danger'
      })
    },

    showLoadingOnElement(element, scale, type) {
      var self = this;
      self.$vs.loading({
        type: type,
        container: self.$refs[element].$el,
        scale: scale
      });
    },
  
    editDataSidebar(data) {
       var self = this;
      //alert(data.id)
     // alert(data.name)
      self.isInfoHidden = false;
      // self.$buildUrlLocal
      //   //Build the report viewer using SyncFusion SDK


        // const element = this.$refs.viewer;
        // element.ejReportViewer({
        //    ajaxBeforeLoad: self.ajaxBeforeLoad
        // });

      //self.$refs.viewer.ejReportViewer({});
        $('#viewer').ejReportViewer({
          
          reportServiceUrl: 'https://localhost:44338/api/ReportApi',
          parameters: [
            { name: 'ReportName', labels: ['ReportName'], values: [data.id] },
            { name: 'Account', labels: ['Account'], values: ['12'] },
            { name: 'Username', labels: ['Username'], values: ['csd'] },
            ],
          processingMode: ej.ReportViewer.ProcessingMode.remote,
          reportPath: 'FAMS',
          ajaxBeforeLoad: self.ajaxBeforeLoad,
          exportProgressChanged: self.onExportProgressChanged,
          reportPrint: self.onReportPrint,
          printMode: true,
          PrintOptions: ej.ReportViewer.PrintOptions.newTab,
          reportExport: self.onReportExport
          //reportPath: report.reportUrl,
          //parameters: [{ name: 'Account', labels: ['Account'], values: [data.id] }]
        })
   
    },
    //********************* Event functions */
     onReportExport : function(args) {
            var exportName = $(args.exportAction).find('#' + this._id + '_exportfileName');
            return exportName.val('DemoReport_01');
        },
    onReportPrint (args) {
             args.printWind.printDelay = 2000;
            args.isStyleLoad = false;
      },
    onExportProgressChanged (args) {
           if (args.stage == "beginExport") {
                    console.log(args.stage);
                    args.format =
                        $('#viewer').ejWaitingPopup({ showOnInit: true, cssClass: "customStyle", text: "Preparing exporting document.. Please wait..." });
                }
                else if (args.stage == "exportStarted") {
                    console.log(args.stage);
                    var popupObj = $('#viewer').data('ejWaitingPopup');
                    popupObj.hide();
                }
                else if (args.stage == "preparation") {
                    console.log(args.stage);
                    console.log(args.format);
                    console.log(args.preparationStage);
                    if (args.format == "PDF" && args.preparationStage == "documentPreparation") {
                        console.log(args.totalPages);
                        console.log(args.currentPage);
                        if (args.totalPages > 1 && args.currentPage > 1) {
                            var progressPercentage = Math.floor((args.currentPage / args.totalPages) * 100);
                            if (progressPercentage > 0) {
                                var popupObj = $('#viewer').data('ejWaitingPopup');
                                popupObj.setModel({ text: "Preparing exporting document.." + progressPercentage + " % completed.. Please wait..." });
                            }
                        }
                    }
                }

                args.handled = true;
      },

      ajaxBeforeLoad: function  (event) {
          event.data = event.model.parameters;
          return event
      },
     cancelAdd() {
        this.isListHidden = false,
        this.isInfoHidden = true;
        this.initValues();
        this.GetGrid();
    },
   addVue() {
        this.initValues();
        this.isInfoHidden = false;
        this.isListHidden = true
        this.GetDropdownOperatorType();
        // this.$refs.wizard.navigateToTab(0)

    },

    saveOperatorParameters(data)
    {
        debugger
    },
     initValues() {
          var self = this;
          self.$refs.wizard.navigateToTab(0)
     },
    //********************* SQL events */
    formSubmitted() {
          var self = this;
          var myobject={};
    },
      
    GetGrid() {
      const self = this;
      self.showLoadingOnElement("gridTable", 1, "radius");
      var onSuccess = function(response) {
        self.reportData = response.data;
      };
      var onFinally = function() {
        self.$vs.loading.close(self.$refs.gridTable.$el);
      };
      self.$ajaxGet(
        self,
          "Reports/GetReportdropdown?AccountId=12&username=csd",
        onSuccess,
        onFinally
      );
    }
  }
};
     
</script>
